<template>
  <div class="site-header-wrap">
    <div class="site-header">
      <a href="javascript:;" class="site-logo"><img src="@/assets/imgs/logo.png" /></a>
      <div class="header-title">{{ $route.meta.title }}</div>
      <div class="header-right"><a href="javascript:;" class="navBtn" @click="toggleNav"><b></b></a></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navToggle: 0
    }
  },
  mounted() {
    this.$eventBus.$on('NavToggle', (v) => {
      this.navToggle = v
    })
  },
  methods: {
    toggleNav() {
      this.$eventBus.$emit('NavToggle', !this.navToggle)
    }
  },
}
</script>

<style lang="scss" scoped>

  .site-header-wrap {height:.5rem;}
  .site-header {position:fixed;top:0;left:0;right:0;z-index:1000;display:flex;height:.5rem;background-color:#fafafa;box-shadow:0 0 5px 0 #bbb;
    .site-logo {padding:.1rem;
      img {height:.3rem;display:block;}
    }
    .header-title {font-size:.2rem;line-height:.5rem;position:absolute;top:0;left:0;right:0;bottom:0;text-align:center;font-family:'DCPText-Bold';}
    .header-right {position:absolute;top:0;right:0;bottom:0;display:flex;
      .navBtn {display:block;width:.5rem;position:relative;
        b {position:absolute;top:50%;left:50%;width:.2rem;height:.02rem;margin:0 0 0 -.1rem;background-color:#999;
          &:before {content:'';position:absolute;top:.06rem;left:0;right:0;height:.02rem;background-color:#999;}
          &:after {content:'';position:absolute;top:-.06rem;left:0;right:0;height:.02rem;background-color:#999;}
        }
      }
    }
  }

</style>
